

@import 'reset';
$accentBlue: #0b7dc2;
$darkGrey: #707c7c;
$lightGrey: #f2f3f4;
$mediumGrey: #babcbe;


@font-face {
	font-family: "Messina-Bold";
	src: url("../fonts/MessinaSans_Web_Bold/MessinaSansWeb-Bold.eot");
	src: url("../fonts/MessinaSans_Web_Bold/MessinaSansWeb-Bold.woff") format("woff"),

}

@font-face {
	font-family: "Messina-Regular";
	src: url("../fonts/MessinaSans_Web_Regular/MessinaSansWeb-Regular.eot");
	src: url("../fonts/MessinaSans_Web_Regular/MessinaSansWeb-Regular.woff") format("woff"),

}

@font-face {
	font-family: "MessinaMono-Bold";
	src: url("../fonts/MessinaSansMono_Web_Bold/MessinaSansMonoWeb-Bold.eot");
	src: url("../fonts/MessinaSansMono_Web_Bold/MessinaSansMonoWeb-Bold.woff");

}


* {
	box-sizing: border-box;
}

body {
	background: #fff;
}






.wrapper {
	border: #ccc 1px solid;
	width: 350px;
	margin: 40px auto;
	position: relative;

	.header {
		padding: 15px;
		background: $lightGrey;
		font-family: "Messina-Bold";
		color: $darkGrey;
	}

	.inside-wrapper {
		width: 335px;
		margin: auto;
		.gender-weight-inputs {
			width: 100%;
			color: $accentBlue;
			font-family: "MessinaMono-Bold";
			padding: 5px;
			

			.gender-radios {
				display: inline-block;
				width: 50%;
				padding: 5px;


			
				p {
					text-align: left;
					color: $accentBlue;
				}
				form.gender-radioBtn {
					margin-top: 5px;
					input[type="radio"] {
						background-color: $accentBlue;
						border: none;
						color: $accentBlue;
					}
				}
			}
			
			 .weight-input {
				display: inline-block;
				padding: 5px;


				p {
					display: inline-block;
				}
				input {

					background: #fff;
					border-radius: 4px;
					width: 50px;
					display: inline-block;
					margin-top: 5px;
					text-align: center;
					font-weight: 100;

				}

				span {
					font-size: 12px;
					color: $darkGrey;
				}
				label {
					display: block;
				}
			}
		}

		h2 {
			color: $accentBlue;
			font-family: "MessinaMono-Bold";
			margin: 4px;
			padding-top: 10px;
			padding-left: 5px;
		}

		.drinks-counter {
			display: flex;
			justify-content: space-evenly;
			
			select {
				border:$accentBlue 1px solid;
				width: 40px;
				color: $accentBlue;
				padding: 1px;
				margin-bottom: 2px;
			}

			.beer-list-wrapper, .wine-list-wrapper, .shots-list-wrapper {
				padding: 10px;
				font-family: "Messina-Bold";
				font-size: 13px;
				

				p{
					padding: 1.5px;
				}

				.alcohol-type {
					color: $accentBlue;
				}

				.alcohol-amount, .alcohol-percent {
					color: $darkGrey;
				}

			}
		}


		.hours-counter {
			margin-bottom: 10px;
			
			.radio-slider {
			margin: 0px;
			font-family: "Messina-Bold";
			font-weight: 300;
			font-size: 12px;
			}

			.radio-custom {
				opacity: 0;
			}

			.border-line {
				position: relative;

			}

			.border-line:before {
				content: "";
				height: 1.7px;
				width: 20px;
				position: absolute;
				left: 98%;
				top: 42%;
				background-color: $accentBlue;
			}

			.border-line:before:focus {
				transform: scale(1.1);
				background-color: red;
			}

			.radio-custom:checked + label,
			.radio-custom:not(:checked) +label {
				position: relative;
				padding-top: 28px;
				cursor: pointer;
				line-height: 20px;
				display: block;
				color: $accentBlue;
			}
			
			.radio-custom:checked + label:before, 
			.radio-custom:not(:checked) + label:before {
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				width: 17px;
				height: 17px;
				border: 1px solid #ddd;
				border-radius: 100%;
				background: #fff;
			}

			.radio-custom:checked + label:after, 
			.radio-custom:not(:checked) + label:after {
				content: '';
				width: 11px;
				height: 12px;
				background: $accentBlue;
				position: absolute;
				top: 4px;
				left: 4px;
				border-radius: 100%;
				transition: all 0.3s ease;
			}

			.radio-custom:not(:checked) + label:after {
				opacity: 0;
				transform: scale(0);
			}

			.radio-custom:checked + label:after {
				opacity: 1;
				transform: scale(1.5);
			}


			h2 {
				color: $accentBlue;

			}

			.checkboxgroup-hours {
				display: inline-block;
				text-align: center;
				padding: 2px;
				margin-top: -15px;
				margin-left: 7px;
				margin-right: 7px;
				margin-bottom: 7px;
			}
			.checkboxgroup-hours label {
				display: block;
			}
		}

		.btn-wrap {
			text-align: center;

			button {
				background-color: $accentBlue;
				border: none;
				color: white;
				padding: 10px 28px;
				text-align: center;
				text-decoration: none;
				display: inline-block;
				font-size: 16px;
				margin: 10px;
				cursor: pointer;
				border-radius: 10px;

			}
			button:hover {
				background-color: $darkGrey;
			}

		}
	}

	

	.card-1, .card-2, .card-3, .card-4, .card-5, .card-6 {
		background-color: $lightGrey;
		width: 100%;



	.card-wrap {
			width: 325px;
			margin: auto;
			padding-left: 10px;
			margin-top: 30px;

			.light-green {
				width: 60px;
				height: 20px;
				background-color: #54b848;
				display: inline-block;
				margin-right: -5px;
			}
			.yellow {
				width: 60px;
				height: 20px;
				background-color: #f6ca17;
				display: inline-block;
				margin-right: -5px;
			}
	
			.orange{
				width: 60px;
				height: 20px;
				background-color: #ef6623;
				display: inline-block;
				margin-right: -5px;
			}
	
			.red {
				width: 60px;
				height: 20px;
				background-color: #cc2127;
				display: inline-block;
				margin-right: -5px;
			}
	
			.black {
				width: 60px;
				height: 20px;
				background-color: #000;
				display: inline-block;
				margin-right: -5px;
			}
	
			p {
				text-align: left;
				padding-bottom: 5px;
				font-family: "Messina-Regular";
				font-size: 15px;
			}

			.bac-color-text-1 {
				color: #54b848;
				margin-top: 10px;
			}
			.bac-info {
				margin-bottom: 10px;
			}
		}
	}

	.card-2 {
		.bac-color-text-2 {
			color: #f6ca17;
			margin-top: 10px;
		}
	}

	.card-3 {
		.bac-color-text-3 {
			color: #ef6623;
			margin-top: 10px;
		}
	}

	.card-4 {
		.bac-color-text-4 {
			color: #cc2127;
			margin-top: 10px;
		}
	}

	.card-5 {
		.bac-color-text-5 {
			color: #cc2127;
			margin-top: 10px;
		}
	}

	.card-6 {
		.bac-color-text-6 {
			color: #f6ca17;
			margin-top: 10px;
		}
	}
}


.card-1, .card-2, .card-3, .card-4, .card-5, .card-6 {
	display: none;
}
